import { useEffect, useState } from 'react';
import './App.css';
import {
    BrowserRouter as Router,
    Route
} from 'react-router-dom';
import { v4 as uuid } from 'uuid';
import { Backdrop, CircularProgress, Typography } from '@material-ui/core';


//Components
import SavedJokes from './components/SavedJokes';
import Jokes from './components/Jokes';
import TopHeader from './components/TopHeader';
import DeleteJoke from './components/DeleteJoke';
import AddJoke from './components/AddJoke';
import BottomFooter from './components/BottomFooter';
import About from './components/About';


//JokeAPI: https://sv443.net/jokeapi/v2/#getting-started

function App() {

  //Variables
  const [jokeData, setJokeData] = useState({
    error: false,
    setup: "",
    delivery: ""
  })
  const [jokeArray, setJokeArray] = useState([]);
  const [jokeDataRetrieved, setJokeDataRetrieved] = useState(false);

  //Open from Local Storage
  const getJokesFromLocal = () => {
    if(localStorage.getItem("jokes")) {
      setJokeArray(JSON.parse(localStorage.getItem("jokes")));
    }
    else {
      setJokeArray([      
        {
        id : uuid(),
        error: false,
        setup: "Tämä on 2 vaiheinen vitsi",
        delivery: "tässä vastaus"
      },
      {
        id : uuid(),
        error: false,
        setup: "",
        delivery: "Tämä on 1-vaiheinen vitsi"
      }
    ])
    }
  }

  //Save jokes to Local Storage
  const setJokesToLocal = () => {
    localStorage.setItem("jokes", JSON.stringify(jokeArray));
  };


  //fetching joke
  const getJoke = async() => {

    try{
  
      const conn = await fetch("https://v2.jokeapi.dev/joke/programming");
      const data = await conn.json();
  
      if(data.type ===  "single"){
        setJokeData({
          id : uuid(),
          error: false,
          setup: "",
          delivery: data.joke,
        });
      }
      else{ 
        setJokeData({
          id : uuid(),
          error: false,
          setup: data.setup,
          delivery: data.delivery
        });
      }

      setJokeDataRetrieved(true);
  
    }

    catch(error){
      setJokeData({
        id : "",
        error: true,
        setup: "",
        delivery: ""
      });
      console.log("error");

    }
  };
  
  //Get jokes when page refresh
  useEffect( () => {
    getJokesFromLocal();
  }, []);

  useEffect( () => {
    getJoke();
  }, [jokeArray]);

  useEffect( () => {
    setJokesToLocal();
  }, [jokeArray]);


  return(
    <>

    <Router>
      <TopHeader/>
      <Route path="/" exact>
        {(jokeData.error)
          ? <Typography variant="h4" color="error">Could not contact server</Typography>
        : (jokeDataRetrieved)
          ? <Jokes 
              jokeData = { jokeData }
              getJoke = { getJoke}
              setJokeArray = { setJokeArray }
              jokeArray = { jokeArray }
          />
          :<Backdrop open={true}>
            <CircularProgress color="inherit" />
          </Backdrop>
        }

      </Route>
      <Route path="/savedjokes">
        <SavedJokes
            setJokeArray = { setJokeArray }
            jokeArray = { jokeArray} 
            /> 
      </Route>
      <Route path="/deletejoke/:id">
        <DeleteJoke
          jokeArray = { jokeArray }
          setJokeArray = { setJokeArray }
        />
      </Route>
      <Route path="/addjoke"
      >
        <AddJoke
          setJokeData = { setJokeData }
          setJokeArray = { setJokeArray }
          jokeArray = { jokeArray }
        />
      </Route>

      <Route path="/about">
        <About/>
      </Route>
      
      <BottomFooter/>

    </Router>



    </>
  )
}

export default App;
