import { Card, CardContent, IconButton, Typography } from "@material-ui/core";
import { useHistory, useParams } from "react-router";
import DeleteForeverIcon from '@material-ui/icons/DeleteForever';
import CancelIcon from '@material-ui/icons/Cancel';

function DeleteJoke(props){

    const history = useHistory();
    const { id } = useParams(); 

    const targetJoke = props.jokeArray.filter((joke) => {
        return (joke.id === id);
    })[0];

    const deleteTargetJoke = () => {
        let helperVar =  props.jokeArray.filter((joke) => {
            return (joke.id !== id);

        });

        props.setJokeArray([...helperVar]);
        history.push("/savedjokes");
    };

    return(
    <div className="jokesDiv">
        <div className="jokeCardWrapper">
            <div className="targetJoke">
                <Typography variant="h3">Delete this joke?</Typography>
                <Card className="jokeCard">
                    <CardContent>
                        <Typography variant="h5">{ targetJoke.setup }</Typography>
                        <Typography variant="h5">{ targetJoke.delivery }</Typography>
                    </CardContent>
                </Card>
            </div>
            <IconButton 
                onClick = { () => { deleteTargetJoke() }}
                variant="contained"
            >
                <DeleteForeverIcon/>
                <Typography variant="h6">Delete</Typography>
            </IconButton>

            <IconButton 
                onClick = { () => { history.push("/savedjokes") }}
                variant="contained"
            >
                <CancelIcon/>
                <Typography variant="h6">Cancel</Typography>
            </IconButton>
        </div>
    </div>
    )
}

export default DeleteJoke;