import { Card, CardContent, IconButton, TextField, Typography } from "@material-ui/core";
import SaveIcon from '@material-ui/icons/Save';
import CancelIcon from '@material-ui/icons/Cancel';
import { useHistory } from "react-router";
import { v4 as uuid } from 'uuid';
import { useState } from "react";





function AddJoke(props){

    const history = useHistory();
    const [userJoke, setUserJoke] = useState("");
    const[error, setError] = useState({
        color: "green",
        text: ""
    })


    const saveJoke = () =>{
        
        const jokeFound = props.jokeArray.some(joke => joke.delivery === userJoke.delivery);

        
        if(!jokeFound){
            props.setJokeArray([...props.jokeArray, userJoke]);
            setError({
                color:"green",
                text: "Saved"
            })
        }
        else{
            setError({
                color:"red",
                text: "This masterpiece is already saved"
            })
        }

        console.log(userJoke.delivery);
    }

    return(
<div className="jokesDiv">
            <div className="jokeCards">
            <Typography variant="h4">Write your own joke</Typography>
                <div className="jokeCardWrapper">
                    <Card className="jokeCard">
                        <CardContent>
                            <Typography variant="h5"></Typography>
                            <TextField
                                label="Write your joke here"
                                multiline
                                rows={4}
                                variant="outlined"
                                fullWidth
                                onChange = { (e) => {
                                    setUserJoke({
                                        id : uuid(),
                                        error: false,
                                        setup: "",
                                        delivery: e.target.value
                                    });
                                    
                                }}
                                />
                           
                        </CardContent>
                    </Card>
                </div>
                    
                    <IconButton
                        aria-label="save"   
                        onClick = { () => {
                            saveJoke();
                        }} 
                        >
                        <SaveIcon/>
                        <Typography variant="h6">Save</Typography>
                    </IconButton> 
                    
                    <IconButton 
                        onClick = { () => { history.push("/") }}
                        variant="contained"
                    >
                        <CancelIcon/>
                        <Typography variant="h6">Cancel</Typography>
                    </IconButton>
                    <Typography variant="h6" 
                    style={{
                        color: error.color
                    }}>{ error.text }</Typography>
            </div>
        </div>
    )
}

export default AddJoke;