import { Card, CardContent, IconButton, Typography } from "@material-ui/core";
import SaveIcon from '@material-ui/icons/Save';
import RefreshIcon from '@material-ui/icons/Refresh';

function Jokes(props){

    //saving Joke:
    const saveJoke = () =>{
        
      props.setJokeArray([...props.jokeArray, props.jokeData]);

  }
    
    console.log(props.jokeData)

    return(
        <div className="jokesDiv">
            <div className="jokeCards">
            <Typography variant="h4">Generate a joke</Typography>
                <div className="jokeCardWrapper">
                    <Card className="jokeCard">
                        <CardContent>
                            <Typography variant="h5">{ props.jokeData.setup }</Typography>
                            <Typography variant="h5">{ props.jokeData.delivery }</Typography>
                        </CardContent>
                    </Card>
                </div>
                    <IconButton 
                        onClick = { () => { props.getJoke() }}
                        variant="contained"
                    >
                        <RefreshIcon/>
                        <Typography variant="h6">Generate New</Typography>
                    </IconButton>
                    
                    <IconButton
                        aria-label="save"   
                        onClick = { () => {
                            saveJoke();      
                        }} 
                        >
                        <SaveIcon/>
                        <Typography variant="h6">Save</Typography>
                    </IconButton> 
            </div>
        </div>
    );
    

};

export default Jokes;