import { Card, CardContent, IconButton, Typography } from "@material-ui/core";
import DeleteIcon from '@material-ui/icons/Delete';
import { Link } from "react-router-dom";

function SavedJokes(props){


    if(props.jokeArray.length > 0){
        return(
            <div className="savedJokes">
                <div className="savedJokesContainer">
                    <Typography variant="h4" className="savedJokesHeader">Saved jokes</Typography>
    
                    { props.jokeArray.map((joke, idx) => (
    
                        <div key={idx} className="jokeCardWrapper">
                            <Card className="jokeCard">
                            <CardContent>
                                <Typography variant="h5">{ joke.setup }</Typography>
                                <Typography variant="h5">{ joke.delivery }</Typography>
                            </CardContent>
                            </Card>
                        
    
                            <IconButton
                                component={Link}
                                to={`/deletejoke/${joke.id}`}
                                variant="contained"
                            >
                                <DeleteIcon/>
                                <Typography variant="h6">DeleteJoke</Typography>
                            </IconButton>
                        </div>
    
                    )) }
                </div>
            </div>
        );
    }
    else{
        return(
            <div className="savedJokes">
            <div className="savedJokesContainer">
                <Typography variant="h4" className="savedJokesHeader">Saved jokes</Typography>
                <Typography variant="h5">There is no saved jokes</Typography>


            </div>
        </div>
        );  
    }


};

export default SavedJokes;