import { AppBar, Drawer, IconButton, List, ListItem, ListItemIcon, Toolbar, Typography } from "@material-ui/core";
import MenuIcon from '@material-ui/icons/Menu';
import HomeIcon from '@material-ui/icons/Home';
import BookmarksIcon from '@material-ui/icons/Bookmarks';
import AddCircleIcon from '@material-ui/icons/AddCircle';
import InfoIcon from '@material-ui/icons/Info';
import { Link } from "react-router-dom";
import { useState } from "react";

function TopHeader() {

    const [showMenu, setShowMenu] = useState(false);

    return(

        <AppBar position ="static" className="topBar">
            <Toolbar variant="dense">
                <IconButton 
                edge="start" className="menuBtn" 
                color="inherit"
                aria-label="menu"
                onClick= { () => {
                    setShowMenu(true)
                }}>
                    <MenuIcon />
                </IconButton>
                <Drawer
                    open={ showMenu }
                    onClose={ () => { setShowMenu(false) } }
                >
                    <List
                        onClick = { () => { setShowMenu(false)}}
                    >
                        <ListItem button component={Link} to="/">
                            <ListItemIcon>
                                <HomeIcon />
                                <Typography variant="h6">Generate a Joke</Typography>
                            </ListItemIcon>
                        </ListItem>
                        
                        <ListItem button component={Link} to="/addJoke">
                            <ListItemIcon>
                                <AddCircleIcon />
                                <Typography variant="h6">Add a Joke</Typography>
                            </ListItemIcon>
                        </ListItem>

                        <ListItem button component={Link} to="/savedjokes">
                            <ListItemIcon>
                                <BookmarksIcon />
                                <Typography variant="h6">Saved Jokes</Typography>
                            </ListItemIcon>
                        </ListItem>

                        <ListItem button component={Link} to="/about">
                            <ListItemIcon>
                                <InfoIcon />
                                <Typography variant="h6">About this App</Typography>
                            </ListItemIcon>
                        </ListItem>
                    </List>
                </Drawer>
                <Typography variant="h6" color="inherit">
                    DevJokes
                </Typography>
            </Toolbar>
        </AppBar>
    );
}

export default TopHeader;