import { Link, makeStyles, Typography } from "@material-ui/core";

//icons:
import LinkedInIcon from '@material-ui/icons/LinkedIn';
import InstagramIcon from '@material-ui/icons/Instagram';
import GitHubIcon from '@material-ui/icons/GitHub';

const useStyles= makeStyles({
        
    icon: {
            fontSize: "80px"
        }
})

function About(){

    const styles = useStyles();

    return(
        <div className="aboutDiv">
            <Typography variant="h3" gutterBottom>About this App</Typography>
            <Typography variant="body1" gutterBottom>
                This app is developed by Joni Laakso for a school assignment.
                App uses <a href="https://sv443.net/jokeapi/v2/#info" rel="noreferrer" target="_blank">JokeApi</a> for fetching jokes.
            </Typography>

            <div className="contactDiv">
                <Typography variant="h3" gutterBottom>Contact:</Typography>
                <Link href="https://www.linkedin.com/in/joni-laakso/" rel="noreferrer" target="_blank"  >
                    <LinkedInIcon className={styles.icon} />
                </Link>
                <Link href="https://www.instagram.com/jonseredix/" rel="noreferrer" target="_blank" >
                    <InstagramIcon  className={styles.icon} style={{color:"#e91e63"}}/>
                </Link>

                <Link href="https://github.com/cjola002-xamk" rel="noreferrer" target="_blank" >
                    <GitHubIcon className={styles.icon} style={{color:"#212121"}}/>
                </Link>
            </div>
        </div>
    );
};

export default About;